import React, { Component } from "react";
import { connect } from "react-redux";
import { Redirect } from "react-router-dom";

import loginActions from "../../../redux/actions/auth/index"
import { UpdatePaths, UpdateMenuConfig } from "../../../redux/actions/menu/index"

class Logout extends Component {

  componentDidMount() {
    this.props.logout();
    this.props.UpdatePaths([]);
    this.props.UpdateMenuConfig([]);
  }

  render() {
    const hasAccessToken  = this.props.auth.token !== undefined;

    return hasAccessToken ? <Redirect to="/" /> : <Redirect to="/auth/login" />;
  }
}

const mapStateToProps = state => {
  return {
    auth: state.auth
  }
}
export default connect(mapStateToProps, { ...loginActions.actions, UpdateMenuConfig, UpdatePaths })(Logout)

